.img{
    max-width: 200px;
    max-height: 200px;
}
.navbar{
    max-width: 80%;
    width:100%;
    justify-content: space-between;
}
.navbar-collapse{
    width: fit-content;
}

.nav-link{
    font-size: 1.25em;
    color: rgba(255, 255, 255, .7) !important;
    text-shadow: 2px 2px rgba(0,0,0,.7);
    transition: all ease-in-out .3s;
}

.nav-link:hover{
    color:white !important;
    text-shadow: none;
}